<template>
  <div>
        <div class='form_div'>
             <div class="layui-form">
                <div>
                    <div class="layui-form-item" style="float:left">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block"> 
                            <input type="text" name="title" required  lay-verify="required" placeholder="" autocomplete="off" class="layui-input" v-model="order_type_name" >
                        </div>
                    </div>
                    <div class='new_order_btn'>
                        <a href="/add_order" class="layui-btn layui-btn-normal" style="float:left"><b></b></a>
                    </div>
                </div>
                <div style="width:510px;">
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block">
                            <select name="department" id="department"  lay-verify="required" lay-search>
                                <option value=""></option>
                                <option value=""></option>
                                <option value="Co">Co</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div>
                    <div class="layui-form-item" >
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block"  style="float:left;margin-left:10px">
                            <input type="radio" name="order_type" v-model="order_type" value="" title="" checked>
                            <input type="radio" name="order_type" v-model="order_type" value="" title="">
                            <input type="radio" name="order_type" v-model="order_type" value="" title="">
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block"  style="float:left;margin-left:10px">
                            <input v-model="order_type_obj" type="radio" name="order_type_obj" value="" title="" checked>
                            <input v-model="order_type_obj" type="radio" name="order_type_obj" value="" title="">
                            <input v-model="order_type_obj" type="radio" name="order_type_obj" value="" title="">
                        </div>
                    </div> 
                </div>
                <div>
                    <div style="float:left;margin-left:30px">
                        <button type="button" class="layui-btn layui-btn-normal" lay-submit @click="search">&emsp;&emsp;<b></b>&emsp;&emsp;</button>
                    </div>
                </div>
                    <br>
                    <br>
                    <br>
                <div>
                    <table  border="1" cellpadding=30>
                        <tr style="height:60px">
                            <td>ID</td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr style="height:100px;" v-for="item in order_list">
                            <td>{{ item.id }}</td>
                            <td>{{ item.status }}</td>
                            <td>{{ item.order_type }}</td>
                            <td>{{ item.accept_department }}</td>
                            <td>
                                {{ item.priority }}
                                {{ item.use_model }}
                                {{ item.application }}
                            </td>
                            <td>{{ item.create_time }}</td>
                            <td>{{ item.update_time }}</td>
                            <td>{{ item.Recipients }}</td>
                            <td>
                                <p><a href="javascript:;" @click="on_jy(item.id,1)" v-if="item.status == ''"></a></p>
                                <p><a href="javascript:;" @click="on_jy(item.id,0)" v-if="item.status == ''"></a></p>
                                <br>
                                <p><a href=""></a></p>
                            </td>
                        </tr>


                    </table>
                </div>
            </div>
	</div>
</div>


</template>


<script>
import axios from 'axios'
import {config2} from '../config.js'
export default {
    data(){
        return{
            order_type_name:'',
            order_type:'',
            order_type_obj:'',
            department:'',
            order_list:[],
        }
    },
    mounted(){
        //  
        this.get_orders();
        //  layui
        layui.use(['element','form'], function(){
            var element = layui.element
            var form = layui.form;
        })
    },
    methods:{
        //  
        on_jy:function(aid,num){
            axios.get(config2.baseurl+'/get_update',{params:{'id':aid,'num':num}}).then(res=>{
                console.log(res.data)
            })
        },
        //  
        get_orders:function(){
            axios.get(config2.baseurl+'/get_orders').then(res=>{
                console.log(res.data)
                this.order_list = res.data.data
            })
        },
        select_depart(val){
            console.log(val)
        },
        // 
         getRadioBoxValue(radioName){
            var obj = document.getElementsByName(radioName);        
            for(let i=0;i<obj.length;i++){
     if(obj[i].checked){
     returnobj[i].value;
     }
    }  
  return "undefined"; 
         } ,
         getSelectValue(selectid){
            var myselect=document.getElementById(selectid);
            var index=myselect.selectedIndex ; 
            return myselect.options[index].value
         },
        search(){
         this.order_type= this.getRadioBoxValue("order_type");   
         this.order_type_obj= this.getRadioBoxValue("order_type_obj");   
            this.department = this.getSelectValue('department')
            //  
            if (this.order_type_name!='' && this.department!='' && this.order_type!='' && this.order_type_obj!=''){
                var data={
                    order_type_name:this.order_type_name,
                    department:this.department,
                    order_type:this.order_type,
                    order_type_obj:this.order_type_obj
                }
                 console.log(data,config2['baseurl'])
                 axios.get(config2.baseurl+'/get_orders',{params:{'query':JSON.stringify([this.order_type_name,this.order_type,this.order_type_obj,this.department])}}).then(res=>{
                console.log(res.data)
                this.order_list = res.data.data
            })
            }else{
                return false
            }
        },

   
    },
    

}

</script>

<style>
.layui-input{
    width: 400px;
}
.form_div{
    margin-top: -20px;
}
.new_order_btn{
    position: fixed;
    right: 50px;
}
/* td{
    width: 100px;
} */
table{
    width: 1400px;
    margin-left: 30px;
    margin-top: 20px;
}
table td{
    width: 100px;
}
</style>



